.tic-toc {
  text-align: center;
  font-size: 80px;
  font-weight: normal;
  cursor: pointer;
  text-shadow: -8px -8px 0 hsl(200,100%,35%),
               -7px -7px 0 hsl(200,100%,55%),
               -6px -6px 0 hsl(200,100%,60%),
               -5px -5px 0 hsl(200,100%,65%),
               -4px -4px 0 hsl(200,100%,70%),
               -3px -3px 0 hsl(200,100%,75%),
               -2px -2px 0 hsl(200,100%,80%),
               -1px -1px 0 hsl(200,100%,85%),
               0 0 0 hsl(200,100%,95%);
  color: transparent;
  transition: all 1s;
}
.tic-tac {
  text-shadow: 0 0 0 ;
  width: 300px;
  height: 300px;
  border: 4px solid black;
  text-align: center;
  font-size: 70px;
  vertical-align: middle;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background-color: rgb(240, 239, 222);
  color: rgb(0, 4, 231);
  }
.ttt-one:hover {
  background-color: rgb(255, 249, 172);
    box-shadow: 0 0 40px #6633FF;
    -moz-box-shadow: 0 0 40px #6633FF;
    -o-box-shadow: 0 0 40px #6633FF;
    -ms-box-shadow: 0 0 40px #6633FF;
    -webkit-box-shadow: 0 0 40px #6633FF;
    opacity: 0.6;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}
.ttt-one:active {
  background-color: #ffd000;
  box-shadow: 0 0 5px rgb(179, 252, 255);
  transform: translateY(1px)
}
.ttt-one{
    line-height: 100px;
    width: 100px;
    height: 100px;
    border: 2px solid  black;
    float: left;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgb(156, 4, 4);
    -moz-box-shadow: 0 0 10px rgb(156, 4, 4);
    -ms-box-shadow: 0 0 10px rgb(156, 4, 4);
    -o-box-shadow: 0 0 10px rgb(156, 4, 4);
    -webkit-box-shadow: 0 0 10px rgb(156, 4, 4);
    
  }